import {
	basePath
} from '@/utils/index'
import api from "@/api/category"
const util = require('@/utils/util.js')
import tuiLoadmore from "@/components/loadmore/loadmore"
import tuiNomore from "@/components/nomore/nomore"
export default {
	components: {
		tuiLoadmore,
		tuiNomore
	},
	data() {
		return {
			basePath,
			tabbar: [],
			subtabbar: [],
			productList: [],
			height: 0, //scroll-view高度
			top: 0,
			currentTab: null, //预设当前项的值
			currentsubTab: null, //预设当前二級分类项的值
			isSHowSub: false, //默认不显示二级分类
			scrollTop: 0, //tab标题的滚动条位置
			searchKey: "", //关键字
			pageIndex:1,
			lastPage:1,
			loadding: false,
			pullUpOn: true,
		}
	},
	onLoad: function(options) {
		setTimeout(() => {
			uni.getSystemInfo({
				success: (res) => {
					let header = 92;
					let top = 10;



					this.height = res.windowHeight - uni.upx2px(header)
					this.top = top + uni.upx2px(header)
					
					//this.getProduct();
					//this.swichNav(this.currentTab)
				}
			});
		}, 50)
		console.log("跳轉參數"+options.searchKey)
		this.currentTab = options.searchKey;			
	},
	onShow() {
		this.getCategory();
		this.getProduct();
	},
	watch: {
		pageIndex(newValue, oldValue) {
			if(this.pageIndex==this.lastPage){
				console.log(22222)
				this.pullUpOn = false;
				
			}
		}
	},
	methods: {
		//获取分类
		getCategory() {
			var param = {
				page: this.$pagination.page,
				limit: this.$pagination.limit,
				name: "",
			}
			this.$postajax(api.getCategory, param)
				.then(res => {
					console.log(JSON.stringify(res))
					if (res.code == 0) {
						this.tabbar = res.data;
						this.tabbar.unshift({
							id: null,
							name: "全部"
						})
					}


				})
				.catch(err => {

				});
		},
		//获取二级分类
		getClass() {
			if (this.currentTab) {
				var param = {
					page: this.$pagination.page,
					limit: this.$pagination.limit,
					/* name: item.name, */
					cid: this.currentTab,
				}
				this.$postajax(api.getClass, param)
					.then(res => {
						//console.log(JSON.stringify(res))
						if (res.code == 0) {
							this.subtabbar = res.data;
							if (res.data && res.data.length > 0) {
								this.isSHowSub = true;
								this.subtabbar.unshift({
									id: null,
									name: "全部"
								})
							} else {
								this.subtabbar = [];
								this.isSHowSub = false;
							}



						}


					})
					.catch(err => {

					});
			} else {
				this.subtabbar = [];
				this.isSHowSub = false;
			}
		},

		//获取产品
		getProduct() {
			var param = {
				page: this.$pagination.page,
				limit: this.$pagination.limit,
				member_id: this.member_id,
				name: this.searchKey,
				class_id: this.currentsubTab,
				category_id: this.currentTab,
			}
			this.$postajax(api.getProduct, param)
				.then(res => {
					if (res.code == 0) {
						this.productList = res.data;
						this.lastPage =res.data&&res.data.length>0?(Math.ceil(res.count/this.$pagination.limit)):1;
						console.log("最后一页"+JSON.stringify(this.lastPage))
						if(this.pageIndex==this.lastPage){
							this.pullUpOn = false;
						}
					}


				})
				.catch(err => {

				});

		},
		// 点击标题切换当前页时改变样式
		swichNav: function(id) {
			//let cur = e.currentTarget.dataset.current;
			/* let cur = index; */
			this.currentTab=id;
			console.log("左侧分类选中数据" + JSON.stringify(id))
			this.currentsubTab =null;
			this.getClass();				
			this.getProduct();
			if (!this.currentTab) {
				this.currentsubTab=null;
				this.isSHowSub = false;
			} 
			if (this.currentTab == id) {
				return false;
			} else {
				this.currentTab = id;
				this.checkCor();
			}
			
			
		},
		swichsubNav(id) {
			this.isSHowSub = false;
			this.currentsubTab = id;
			this.getProduct();
		},
		//判断当前滚动超过一屏时，设置tab标题滚动条。
		checkCor: function() {
			let that = this;
			//这里计算按照实际情况进行修改，动态数据要进行动态分析
			//思路：窗体高度/单个分类高度 200rpx 转px计算 =>得到一屏幕所显示的个数，结合后台传回分类总数进行计算
			//数据很多可以多次if判断然后进行滚动距离计算即可
			if (that.currentTab > 7) {
				that.scrollTop = 500
			} else {
				that.scrollTop = 0
			}
		},
		detail(item) {
			this.isSHowSub = false;
			console.log(JSON.stringify(item))
			uni.navigateTo({
				url: '../extend-view/productDetail/productDetail?id=' + item.id
			})
		},
		//模糊搜索
		search: function() {
			this.getProduct();
		},
		//清空搜索关键字
		cleanKey: function() {
			this.searchKey = '';
			this.getProduct();
		},
		onReachBottom: function() {
			console.log(3333)
			//if (!this.pullUpOn) return;
			this.pullUpOn = true
			this.loadding = true;
			console.log('页面上拉触底事件');
			// 每次拉到页面底部,请求页数自加
			this.pageIndex++;
			// 第二次之后请求数据
			if(this.pageIndex>this.lastPage){
				setTimeout(() => {
					this.loadding = false
					this.pullUpOn = false
				}, 1000)
				return false;
			}else{
			var param = {
				page: this.pageIndex,
				limit: this.$pagination.limit,
				member_id:this.member_id,
				name: this.searchKey,
			}
			this.$postajax(api.getProduct,param)
				.then(res => {
					console.log(JSON.stringify(res))
					if (res.code == 0) {
						console.log(JSON.stringify(res))
						if (res.code == 0) {
							this.productList = this.productList.concat(res.data)
							this.loadding = false;
						}
					}
			
			
				})
				.catch(err => {
			
				});
			}
		}
	}
}
